<template>
	<div>
		<!-- active-name==谁，谁就被选中 -->
		<Menu mode="horizontal" theme="dark" :active-name="navItemActive">
			<Row class='container'>
				<li>
					<a href="/" class="header-nav-logo">
						<img src="../../static/img/header-nav-logo.png">
					</a>
				</li>
				<MenuItem name="home" :to="{name:'Vhome'}">
					<Icon type="ios-home" />
					首页
				</MenuItem>
				<!-- 
					TODO:
					点击时调用ip检测返回对应ip所在地城市的city_info, 这里可以绑定
				 -->
				<MenuItem name="info" :to="{name:'Vcity_info', params: {city_name: 'NULL'}}">
					<Icon type="ios-information-circle-outline" />
					空气质量信息
				</MenuItem>
				<MenuItem name="predict" :to="{name:'Vpredict'}">
					<Icon type="ios-pulse" />
					空气质量预测
				</MenuItem>
				<MenuItem name="mana" :to="{name:'Vmana'}">
					<Icon type="md-settings" />
					数据管理
				</MenuItem>
				<MenuItem name='search' class='header-nav-search'>
					<Input search placeholder="输入查询的地区..." size='large'/>
				</MenuItem>
			</Row>
		</Menu>
		<router-view />
		<Vbacktop></Vbacktop>
		<Vfooter></Vfooter>
	</div>
</template>

<script>
	import Vbacktop from './Vbacktop.vue' //要记得挂载
	import Vfooter from './Vfooter.vue'
	export default {
		name: 'Vmain',
		data() {
			return {
			};
		},
		mounted() {
		/* 	//将uri提交
			this.$store.commit('mut_updateNavItemActive', this.$route.path.split('/')[2]); */
		},
		computed: {
			navItemActive() {
				return this.$store.getters.navItemActive;
			}
		},
		components: {
			Vbacktop,
			Vfooter,
		}
	}
	
	
</script>

<style scoped>
	.header-nav-logo {
		    height: 50px;
			margin-top: 8px;
			float: left;
	}
	
	.header-nav-logo img {
		height: 100%;
	}
	
	.header-nav-search {
		float: right;
	}
</style>



